        .row.justify-content-center.align-items-center(data-aos="fade-down-right")
          .col-sm-6.col-3.px-0.px-sm-5.sobre.ms-3
            img(src='@/assets/curso/tema1/figura26.png')
          .col-sm-6.col-9.correr-izq2.ps-5.ps-sm-0
            p.padding3-y.fondo-18.text-center <em>Reporting</em>
    .row.justify-content-center.my-5
      .col-lg-10.col-md-12
        .row.justify-content-center.align-items-center.fondo-10.ps-3.pe-5.py-4.redondearpuntas.mb-4
          .col-md-2.col-sm-4.col-5.p-4
            img(src='@/assets/curso/tema1/figura13.svg')
          .col-xxl-8.col-md-7.text-white
            h3.mb-0 PTES
            p.mb-4.mb-md-0 Su consulta para conocer más a detalle esta metodología puede ser a través de su sitio oficial, el cual también se encuentra en el material complementario.
          .col-xxl-2.col-md-3
            a.boton.color-acento-botones(:href="('http://www.pentest-standard.org/index.php/PTES_Technical_Guidelines')" target="_blank" type="application/pdf")
              span Ir al sitio <i>web</i>
              i.fas.fa-file-download
    p(data-aos="fade-down-right") Las metodologías anteriormente descritas permitirán realizar un análisis de vulnerabilidades, establecer planes de acciones y un proceso importante, que es adoptar planes de mejoramiento al interior, dado que el proceso de gestión de vulnerabilidades debe ser un proceso continuo.
    separador
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.2 Tipos de pruebas
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-2.col-md-3.col-sm-4.col-5.mb-4.mb-lg-0(data-aos="fade-down-right")
        img(src='@/assets/curso/tema1/figura27.svg')
      .col-lg-10.col-md-9.col-sm-12(data-aos="fade-down-left")
        p Cuando se está desarrollando el proceso de evaluación de la seguridad, se debe de garantizar que los resultados presentados estén respaldados en ejercicios de verificación y comprobación que permitan establecer si una condición se está cumplimento o no; para este caso estamos hablando de controles, técnicas, tácticas para el aseguramiento de la información y en este ejercicio pueden surgir diferentes alternativas para verificar este tipo de cumplimientos, entre las que se pueden apreciar:
    SlyderF(data-aos="fade-down-right").mb-5(columnas="col-lg-6 col-xl-4")
      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura28.svg')
          .col-8
            h4 Pruebas unitarias
        p Este tipo de pruebas se enfocan en testear fragmentos de código o aplicaciones para evaluar la lógica bajo la cual se ha programado y si desarrolla adecuadamente las tareas bajo ciertas condiciones.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura29.svg')
          .col-8
            h4 Prueba de integración 
        p Estas pruebas, buscan verificar el desarrollo de una actividad que involucra más de un componente, por ejemplo, la lógica de una aplicación <i>web</i> y la de un motor de base de datos.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura30.svg')
          .col-8
            h4  Pruebas de extremo a extremo
        p Estas buscan verificar el traslado de información y cómo viajan entre un punto de origen y uno de destino, este tipo de pruebas son comunes cuando se prueban API’s o plataformas de envío de mensajes.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura31.svg')
          .col-8
            h4  Prueba de aceptación
        p Este tipo de pruebas, busca validar si una solución cumple con lo requerido o solicitado por un cliente.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura32.svg')
          .col-8
            h4  Pruebas de caja blanca
        p Este tipo de pruebas validan el comportamiento de la lógica de la solución, para realizar este tipo de pruebas, se asume que conoce los detalles de su arquitectura, métodos, funciones entre otros.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura33.svg')
          .col-8
            h4 Pruebas de caja negra
        p Este tipo de pruebas son de funcionalidad y comportamiento de un sistema, por lo general desconociendo la estructura de la solución.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura34.svg')
          .col-8
            h4  Pruebas de caja gris
        p Propone una validación, combinando las pruebas de caja blanca y caja negra, permitiendo abordar aspectos más complejos e integrales en las soluciones.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura35.svg')
          .col-8
            h4  Prueba manual
        p Son pruebas que se especifican manualmente y sin automatización, para la evaluación de los resultados obtenidos.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura36.svg')
          .col-8
            h4 Prueba estática
        p Son aquellas que se realizan sin código real, para determinar si hay defectos en el rendimiento de la solución.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura37.svg')
          .col-8
            h4 Pruebas dinámicas
        p Este tipo de pruebas se ejecuta con código real para validar su funcionamiento.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura38.svg')
          .col-8
            h4 Pruebas visuales o de interfaz de usuario
        p Son las pruebas que se realizan a interfaces de usuario para la revisión del comportamiento y la integridad de la información 

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura39.svg')
          .col-8
            h4 Pruebas de humo
        p Tipo de prueba que se realiza a un conjunto pequeño de controles para verificar su funcionalidad

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura40.svg')
          .col-8
            h4 Pruebas de regresión
        p Permite validar si una funcionalidad o característica se rompe o no está funcionando como se propuso.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura41.svg')
          .col-8
            h4 Pruebas de carga
        p Este tipo de pruebas buscan verificar el comportamiento de una solución de acuerdo a diferentes momentos de demanda del servicio.

      .tarjeta.sinredondeo.ajustesize.fondo-19.p-4.m-auto
        .row.justify-content-center.mb-3.align-items-center
          .col-4
            img(src='@/assets/curso/tema1/figura42.svg')
          .col-8
            h4 Pruebas de inserción
        p Este tipo de pruebas permite verificar la integridad de la seguridad de la información dentro de la solución.
    .row.fondo-20.align-items-center
      .col-lg-8.col-md-12.mb-lg-2.mb-lg-4.p-lg-5.p-3(data-aos="fade-down-right")
        p.mt-0.mt-lg-3.mt-xl-0 Existen varios tipos de pruebas que se pueden realizar a las aplicaciones o soluciones con las que cuenta la organización que permite verificar si realmente están realizando las operaciones según lo solicitado y lo establecido en el programa.
        p Su aplicación dependerá del alcance de la evaluación a realizar, así como también dependerá si las soluciones son construidas <i>in-house</i> o son adquiridas a un tercero, en cualquiera de los casos, debería de validarse la información para garantizar su integridad
      .col-lg-4.col-md-6.col-sm-7.col-8.pe-0.pb-0.my-5.mostrar-lg(data-aos="fade-down-left")
        img.subir(src='@/assets/curso/tema1/imagen4.png')
    separador
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.3 <em>Hacking</em> ético
    figure.mb-5(data-aos="fade-up-left")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/8q3cb4QwJSk" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen) 
    .row.justify-content-center.mb-5
      .col-lg-5.col-md-6.col-sm-7.col-8.mb-4.mb-lg-0(data-aos="fade-down-right")
        img(src='@/assets/curso/tema1/imagen5.png')
      .col-lg-7.col-md-12(data-aos="fade-down-left")
        p El <i>hacking</i> ético o también denominado sombrero blanco, es un conjunto de técnicas que se aplican para el descubrimiento de vulnerabilidades dentro de un entorno o un conjunto de aplicaciones.
        p Esta técnica se desarrolla de manera consentida y aprobada por las organizaciones y los responsables de realizar las validaciones correspondientes, con lo cual buscan identificar las debilidades, anormalidades y falencias en su seguridad.
        p Los expertos que realizan este tipo de validaciones hacen uso de técnicas especializadas que se denominan <em>Test</em> de Penetración o <em>Pentesting</em>, y su objetivo es burlar los controles de seguridad y en caso de conseguir una identificación positiva, este deberá reportarlo a la organización. Este tipo de estrategia no debería de causar daño, dado que es concebido y tiene fines de exploración.
    figure.my-5(data-aos="fade-up-left")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/r3S8tscj-bg" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)    
    p.my-5(data-aos="fade-down-right") Este ejercicio se desarrolla básicamente en las siguientes 5 fases:
    .titulo-sexto.color-acento-contenido.mt-5(data-aos="fade")
      h5 Figura 4. 
      span Fases del <em>hacking</em> ético
    .row.justify-content-center.fondo-21
      .col-12
        SlyderF(data-aos="fade-down-right").my-5(columnas="col-lg-6 col-xl-4")
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura43.svg')
            h4 Fase 1 - Reconocimiento
            p Preparación del escenario, hace uso de diferentes técnicas para el descubrimiento de pistas que le permitan alcanzar su objetivo.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                p Ingeniería social
              li.mb-0
                i.lista-ul__vineta
                p <i>Dumpster diving</i>
              li.mb-0
                i.lista-ul__vineta
                p Footprinters
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura44.svg')
            h4 Fase 2 - Escaneo
            p A partir de la información recolectada, inicia con el escaneo con la ayuda de herramientas para la búsqueda de vulnerabilidades.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                | Escáner de puertos
              li.mb-0
                i.lista-ul__vineta
                | Escáner de vulnerabilidades
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura45.svg')
            h4 Fase 3 - Obtener acceso
            p Fase en la cual explota las vulnerabilidades encontradas.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                | Inyección de código
              li.mb-0
                i.lista-ul__vineta
                | Basado en <i>web</i>
              li.mb-0
                i.lista-ul__vineta
                | Basado en red
              li.mb-0
                i.lista-ul__vineta
                | Ingeniería social
              li.mb-0
                i.lista-ul__vineta
                | Ataque de contraseñas
              li.mb-0
                i.lista-ul__vineta
                | <i>Network spoofung</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Network sniffers</i>
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura46.svg')
            h4 Fase 4 - Mantener acceso
            p Fase en donde mantiene el sistema vulnerable.
            ul.lista-ul
              li.mb-0
                i.lista-ul__vineta
                | <i>Back Doors</i>
              li.mb-0
                i.lista-ul__vineta
                | Troyanos
              li.mb-0
                i.lista-ul__vineta
                | <i>Rootkit</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Keyloggers</i>
              li.mb-0
                i.lista-ul__vineta
                | <i>Botnets</i>
          .tarjeta.ajustesize.fondo-22.p-4.m-auto
            .row.mb-3
              .col-4
                img(src='@/assets/curso/tema1/figura47.svg')
            h4 Fase 5 - Cubrir huella
            p Fase en la cual cubre todas las pistas de las acciones realizadas durante el ejercicio.
            ul.lista-ul
              li.mb-0 
                i.lista-ul__vineta
                | Troyanos
              li.mb-0
                i.lista-ul__vineta
                | <i>Rootkit</i>
              li.mb-0
                i.lista-ul__vineta
                | Esteganografía
              li.mb-0
                i.lista-ul__vineta
                | <i>Tuneling</i>         
    p.my-5(data-aos="fade-down-right") Ahora bien, dependiendo de su alcance, se puede determinar el tipo de <em>hacker</em> requerido, tal como se evidencia:
    figure(data-aos="fade-up-left")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/Puk3JZ5R_Ic" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    .row.mostrar-lg.my-5(data-aos="fade-down-right")
      .col-12
        img(src='@/assets/curso/tema1/figura48.svg')
    .row.mostrar-movil.my-5.justify-content-center
      .col-md-6.col-sm-8.col-8
        img.mb-4(src='@/assets/curso/tema1/figura49.svg')
        img(src='@/assets/curso/tema1/figura50.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas
        p.mb-0.py-3.px-4 Enemigo del <em>Black Hat</em>, este destruye lo que este construye.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura51.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 <em>Hacker</em> bueno que realiza tareas de descubrimiento de vulnerabilidades.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura52.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 Realiza acciones entre buenas y malas.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura53.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 <em>Hacker</em> con intenciones de hacer daño.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura54.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 Persona que informa o entrega información a terceros como la competencia, por ejemplo.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura55.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 <em>Hacker</em> que buscan errores en soluciones <em>software</em> para su mejora.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura56.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 Dedicado al <em>hackeo</em> de cuentas de redes sociales.
      .col-md-6.col-sm-8.col-8.mt-4
        img(src='@/assets/curso/tema1/figura57.svg')
      .col-md-7.col-sm-8.col-10.fondo-23.redondearpuntas.p-2
        p.mb-0.py-3.px-4 Novatos en el ejercicio del <em>hacking</em>.
    p.my-5(data-aos="fade-down-right") Estos ejercicios son muy comunes cuando las organizaciones quieren identificar vulnerabilidades presentes en sus sistemas, y requieren que una persona externa desarrolle esta actividad, pero siempre mediando algún acuerdo de confidencialidad y naturalmente una autorización de por medio.
    separador
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-down-left")
      h2 1.4  Equipos de seguridad <em>Red Team & Blue Team</em>
    .row.justify-content-center.my-5
      .col-lg-8.col-md-12.mb-4.mb-lg-0(data-aos="fade-down-right")
        p Los equipos de seguridad <em>Red Team & Blue Team</em> se consolidan como estrategias para organizaciones más complejas y de mayor tamaño y como su nombre lo indica, está conformado por equipos de personas quienes, desde cada uno de sus grupos, realizan acciones para la identificación de vulnerabilidades.
        p Este concepto proviene de elementos relacionados con entrenamiento militar, los cuales fueron adoptados por la disciplina de la ciberseguridad y que incorporaron para establecer estrategias de auditoría desde dos frentes distintos. Veamos:
      .col-md-4.col-sm-7.col-8(data-aos="fade-down-left")
        img(src='@/assets/curso/tema1/figura58.svg')
    .row
      .col-auto.fondo-24.py-3.px-5(data-aos="fade-down-right")
        h3.mb-0.text-white.ms-4 Comparación entre el <em>Red Team</em> y el <em>Blue Team</em>
    .row.justify-content-between.my-5(data-aos="fade-down-right")
      .col-lg-6.col-md-12.mb-4.mb-lg-0.fondo-25.redondearpuntas.pt-0.separar(data-aos="fade-down-right")
        .row.justify-content-cent
        er.mt-0
          .col-4.pt-0.subir1
            img.mt-0(src='@/assets/curso/tema1/figura59.svg')
          .col-8.pt-4
            h5 <em>Red Team</em>
            p Busca burlar la seguridad de la organización, y simular ataques para la identificación de vulnerabilidades, haciendo uso de técnicas de <em>pentesting</em>, herramientas especializadas e incluso de técnicas de recolección de información convencionales, todo esto realizándose desde el exterior de la organización; lo anterior teniendo en cuenta:
        .row.justify-content-center
          .col-md-6.col-sm-12.mb-4.mb-lg-0.ps-4(data-aos="fade-down-right")
            ul.lista-ul
              li 
                i.fas.fa-check-circle
                | Seguridad ofensiva
              li 
                i.fas.fa-check-circle
                | <em>Hackeo</em> ético
              li 
                i.fas.fa-check-circle
                | Explotación de vulnerabilidades
              li 
                i.fas.fa-check-circle
                | Pruebas de penetración
              li 
                i.fas.fa-check-circle
                | Pruebas de caja negra
              li 
                i.fas.fa-check-circle
                | Ingeniería social
              li 
                i.fas.fa-check-circle
                | Escaneo de aplicaciones <i>web</i> 
          .col-md-6.col-sm-7.col-8(data-aos="fade-down-left")
            img(src='@/assets/curso/tema1/figura60.svg')
      .col-lg-6.col-md-12.fondo-26.redondearpuntas.separar.pt-0(data-aos="fade-down-left")
        .row.justify-content-center.mt-0
          .col-4.pt-0.subir1
            img.mt-0(src='@/assets/curso/tema1/figura61.svg')
          .col-8.pt-4
            h5 <em>Blue Team</em>
            p Hacen frente a los ataques y defienden a la organización de los posibles riesgos de seguridad de la información, convirtiéndose en una primera línea frente a la atención a cualquier incidente de seguridad; teniendo en cuenta:
        .row.justify-content-center.mt-4
          .col-md-6.col-sm-12.mb-4.mb-lg-0.ps-4(data-aos="fade-down-right")
            ul.lista-ul
              li 
                i.fas.fa-check-circle
                | Seguridad defensiva
              li 
                i.fas.fa-check-circle
                | Protección de infraestructura
              li 
                i.fas.fa-check-circle
                | Control de daños
              li 
                i.fas.fa-check-circle
                | Respuesta al incidente
              li 
                i.fas.fa-check-circle
                | Seguridad operacional
              li 
                i.fas.fa-check-circle
                | Caza de amenazas
